import axios from "axios";
import { GET_CHALLENGES, GET_ERRORS } from "./types";

// Get all challenges
export const getChallenges = () => dispatch => {
  axios
    .get("/api/challenges")
    .then(res => {
      dispatch({
        type: GET_CHALLENGES,
        payload: res.data
      });
    })
    .catch(err =>
      dispatch({
        type: GET_ERRORS,
        payload: err.response.data
      })
    );
};
