import React, { Component } from "react";
import { Card, Input, Icon, Label } from "semantic-ui-react";
import { connect } from "react-redux";
import axios from "axios";
import isEmpty from "../../../../validation/is-empty";

import "./Challenge.css";

class Challenge extends Component {
  state = {
    show: false,
    flag: "",
    errors: {},
    solved: false
  };
  componentDidMount() {
    this.setState({
      solved:
        this.props.challenge.solvedBy.filter(
          team => team._id.toString() === this.props.auth.team.id.toString()
        ).length > 0
    });
  }
  onChangeHandler = event => {
    this.setState({
      [event.target.name]: event.target.value
    });
  };
  onFlagSubmit = () => {
    axios
      .post(`/api/submissions/submit/${this.props.challenge._id}`, {
        flag: this.state.flag
      })
      .then(res => {
        if (res.data.verdict === "Wrong") {
          this.setState({
            errors: { verdict: res.data.verdict }
          });
        } else {
          if (res.data.verdict === "Correct") {
            this.props.getChallenges();
          }
          this.setState({
            errors: { verdict: res.data.verdict },
            solved: true
          });
        }
      })
      .catch(err => {
        this.setState({
          errors: err.response.data
        });
      });
  };

  onClick = () => {
    this.setState({
      show: !this.state.show,
      flag: "",
      errors: {}
    });
  };
  render() {
    const { name, value, description } = this.props.challenge;
    const { errors } = this.state;
    let errorLabel;
    let solved_input = true;

    if (isEmpty(errors)) {
      errorLabel = null;
    } else {
      if (isEmpty(errors.verdict)) {
        solved_input = false;
        errorLabel = (
          <Label basic color="red" size="large" pointing>
            {errors.flag}
          </Label>
        );
      } else {
        if (errors.verdict === "Wrong") {
          solved_input = false;
          errorLabel = (
            <Label basic color="red" size="large" pointing>
              Ooopss. Incorrect
            </Label>
          );
        } else {
          errorLabel = (
            <Label basic color="green" size="large" pointing>
              Correct Answer
            </Label>
          );
        }
      }
    }

    return (
      <Card
        raised
        inverted
        fluid={this.state.show}
        style={{
          fontFamily: "Iceland",
          fontSize: "15px",
          borderRadius: "25px"
          // backgroundColor: this.state.solved ? "#E8F5E9" : "white",
          // textAlign: "center"
        }}
        size="big"
        color={this.props.color}
        link
      >
        <Card.Content onClick={this.onClick}>
          <Card.Header>
            {name}
            {this.state.solved ? (
              <Icon name="check circle outline" color="green" />
            ) : null}
          </Card.Header>
          <Card.Meta style={{ fontSize: "30px" }}>{`${value}`}</Card.Meta>
          {/* {this.state.show ? (
            <Card.Description>{description}</Card.Description>
          ) : null} */}
        </Card.Content>
        {this.state.show ? (
          <Card.Content extra>
            <Card.Description style={{ fontSize: "20px" }}>
              {description}
            </Card.Description>
            <Input
              style={{
                margin: "20px 0px 0px 0px"
              }}
              size="medium"
              icon={
                <Icon
                  inverted
                  color="green"
                  circular
                  link
                  name="send"
                  onClick={this.onFlagSubmit}
                />
              }
              error={!solved_input}
              fluid
              placeholder="Enter the Flag"
              name="flag"
              value={this.state.flag}
              onChange={this.onChangeHandler}
            />
            {errorLabel}
          </Card.Content>
        ) : null}
      </Card>
    );
  }
}

const mapStateToProps = state => ({
  auth: state.auth
});

export default connect(mapStateToProps)(Challenge);
