import React, { Component } from "react";
import { Button, Form, Segment } from "semantic-ui-react";
import isEmpty from "../../validation/is-empty";
import { connect } from "react-redux";
import { loginTeam } from "../../actions/authAction";
import PropTypes from "prop-types";

import "./Login.css";

class Login extends Component {
  state = {
    handle: "",
    password: "",
    errors: {}
  };

  componentWillMount() {
    if (this.props.auth.isAuthenticated) {
      this.props.history.push("/dashboard");
    }
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.auth.isAuthenticated) {
      this.props.history.push("/dashboard");
    }
    if (nextProps.errors) {
      this.setState({
        errors: nextProps.errors
      });
    }
  }

  onChangeHandler = event => {
    this.setState({
      [event.target.name]: event.target.value
    });
  };

  onSubmitHandler = event => {
    const teamData = {
      handle: this.state.handle,
      password: this.state.password
    };
    this.props.loginTeam(teamData);
  };

  render() {
    const errors = this.state.errors;

    return (
      <Segment
        raised
        style={{
          fontFamily: "Iceland",
          margin: "100px auto 0 auto",
          width: "500px",
          borderRadius: "10px",
          border: "green"
        }}
      >
        <Form>
          <h1>Login</h1>
          <hr />
          <label>Team Handle</label>
          <br />
          {isEmpty(errors.handle) ? null : <span>{errors.handle}</span>}
          <Form.Input
            fluid
            style={{
              fontFamily: "Iceland",
              fontSize: "15px"
            }}
            placeholder="Team handle"
            name="handle"
            value={this.state.handle}
            onChange={this.onChangeHandler}
            error={!isEmpty(errors.handle)}
          />
          <label>Password</label>
          <br />
          {isEmpty(errors.password) ? null : <span>{errors.password}</span>}
          <Form.Input
            fluid
            style={{
              fontFamily: "Iceland",
              fontSize: "15px"
            }}
            placeholder="Password"
            name="password"
            value={this.state.password}
            type="password"
            onChange={this.onChangeHandler}
            error={!isEmpty(errors.password)}
          />
          <Button
            inverted
            color="green"
            type="submit"
            style={{
              fontFamily: "Iceland",
              fontSize: "25px",
              padding: "10px",
              width: "100%",
              margin: "10px 0px 0px 0px"
            }}
            onClick={this.onSubmitHandler}
          >
            Submit
          </Button>
        </Form>
      </Segment>
    );
  }
}

Login.propTypes = {
  loginTeam: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  auth: state.auth,
  errors: state.errors
});

export default connect(
  mapStateToProps,
  { loginTeam }
)(Login);
