import React, { Component } from "react";
import { Table, Container } from "semantic-ui-react";
import { connect } from "react-redux";
import axios from "axios";

class Leaderboard extends Component {
  state = {
    teams: []
  };
  componentDidMount() {
    axios
      .get("/api/teams/leaderboard")
      .then(res => {
        this.setState({
          teams: res.data
        });
      })
      .catch(err => console.log(err));
  }

  render() {
    const { teams } = this.state;

    const leaderboardContent = teams.map((team, index) => (
      <Table.Row key={index} positive={team._id === this.props.auth.team.id}>
        <Table.Cell>{index + 1}</Table.Cell>
        <Table.Cell>{team.name}</Table.Cell>
        <Table.Cell>{team.handle}</Table.Cell>
        <Table.Cell>{team.score}</Table.Cell>
      </Table.Row>
    ));

    return (
      <Container
        style={{
          margin: "20px auto"
        }}
      >
        <Table color="teal" padded celled>
          <Table.Header>
            <Table.Row>
              <Table.HeaderCell>Rank</Table.HeaderCell>
              <Table.HeaderCell>Team Name</Table.HeaderCell>
              <Table.HeaderCell>Handle</Table.HeaderCell>
              <Table.HeaderCell>Score</Table.HeaderCell>
            </Table.Row>
          </Table.Header>
          <Table.Body>{leaderboardContent}</Table.Body>
        </Table>
      </Container>
    );
  }
}

const mapStateToProps = state => ({
  auth: state.auth
});

export default connect(mapStateToProps)(Leaderboard);
