import React, { Component } from "react";

import Moment from "react-moment";

import { Container, Segment, Header } from "semantic-ui-react";

class Clock extends Component {
  render() {
    let { start, end } = this.props;
    start = new Date(start);
    end = new Date(end);
    return (
      <Container>
        <Segment raised inverted padded color="">
          <Header as="h1" style={{ fontFamily: "Iceland", fontSize: "40px" }}>
            Starts: <Moment format="h:mm a">{start}</Moment> on{" "}
            <Moment format="MMMM DD, YYYY">{start}</Moment>
          </Header>

          <Header as="h1" style={{ fontFamily: "Iceland", fontSize: "40px" }}>
            Ends: <Moment format="h:mm a">{end}</Moment> on{" "}
            <Moment format="MMMM DD, YYYY">{end}</Moment>
          </Header>
        </Segment>
      </Container>
    );
  }
}

export default Clock;
