import React, { Component } from "react";
import InertiaLogo from "./InertiaLogo/InertiaLogo";
import { Menu, Icon } from "semantic-ui-react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { logoutTeam } from "../../actions/authAction";
import PropTypes from "prop-types";

import "./Navbar.css";

class Navbar extends Component {
  state = {
    activeItem: ""
  };
  componentDidMount() {
    this.setState({
      activeItem: window.location.href.split("/")[
        window.location.href.split("/").length - 1
      ]
    });
  }
  onLogoutHandler = () => {
    this.props.logoutTeam();
  };
  onMenuItemClick = (e, { name }) => {
    this.setState({
      activeItem: name
    });
  };
  render() {
    const { activeItem } = this.state;
    let navigations = (
      <Menu.Menu position="right">
        <Menu.Item
          name="rules"
          active={activeItem === "rules"}
          onClick={this.onMenuItemClick}
          style={{
            fontFamily: "Iceland",
            fontSize: "30px",
            padding: "0px",
            margin: "0px 15px 0px 15px"
          }}
        >
          <Link
            to="/rules"
            style={{
              color: "green"
            }}
          >
            Rules
          </Link>
        </Menu.Item>

        <Menu.Item
          name="login"
          active={activeItem === "login"}
          onClick={this.onMenuItemClick}
          style={{
            fontFamily: "Iceland",
            fontSize: "30px",
            padding: "0px",
            margin: "0px 15px 0px 15px"
          }}
        >
          <Link
            to="/login"
            style={{
              color: "orange"
            }}
          >
            Login
          </Link>
        </Menu.Item>
        <Menu.Item
          name="register"
          active={activeItem === "register"}
          onClick={this.onMenuItemClick}
          style={{
            fontFamily: "Iceland",
            fontSize: "30px",
            padding: "0px",
            margin: "0px 15px 0px 15px"
          }}
        >
          <Link
            to="/register"
            style={{
              color: "orange"
            }}
          >
            Register
          </Link>
        </Menu.Item>
      </Menu.Menu>
    );

    if (this.props.auth.isAuthenticated) {
      navigations = (
        <Menu.Menu position="right">
          <Menu.Item
            name="dashboard"
            active={activeItem === "dashboard"}
            onClick={this.onMenuItemClick}
            style={{
              fontFamily: "Iceland",
              fontSize: "30px",
              padding: "0px",
              margin: "0px 15px 0px 15px"
            }}
          >
            <Link
              to="/dashboard"
              style={{
                color: "orange"
              }}
            >
              Dashboard
            </Link>
          </Menu.Item>
          <Menu.Item
            name="challenges"
            active={activeItem === "challenges"}
            onClick={this.onMenuItemClick}
            style={{
              fontFamily: "Iceland",
              fontSize: "30px",
              padding: "0px",
              margin: "0px 15px 0px 15px"
            }}
          >
            <Link
              to="/challenges"
              style={{
                color: "orange"
              }}
            >
              Challenges
            </Link>
          </Menu.Item>
          <Menu.Item
            name="leaderboard"
            active={activeItem === "leaderboard"}
            onClick={this.onMenuItemClick}
            style={{
              fontFamily: "Iceland",
              fontSize: "30px",
              padding: "0px",
              margin: "0px 15px 0px 15px"
            }}
          >
            <Link
              to="/leaderboard"
              style={{
                color: "orange"
              }}
            >
              Leaderboard
            </Link>
          </Menu.Item>
          <Menu.Item
            name="notification"
            active={activeItem === "notification"}
            onClick={this.onMenuItemClick}
            style={{
              fontFamily: "Iceland",
              fontSize: "30px",
              padding: "0px",
              margin: "0px 0px 0px 0px"
            }}
          >
            <Link
              to="/notifications"
              style={{
                color: "blue"
              }}
            >
              <Icon name="bell" color="blue" />
            </Link>
          </Menu.Item>
          <Menu.Item
            name="logout"
            active={activeItem === "logout"}
            onClick={this.onLogoutHandler}
            style={{
              fontFamily: "Iceland",
              fontSize: "30px",
              padding: "0px",
              margin: "0px 0px 5px 15px"
            }}
          >
            <Icon name="sign-out" color="red" />
          </Menu.Item>
        </Menu.Menu>
      );
    }

    return (
      <div className="Navbar">
        <Menu pointing secondary>
          <Menu.Menu position="left">
            <Menu.Item
              name="home"
              onClick={this.onMenuItemClick}
              style={{ padding: "0px" }}
            >
              <InertiaLogo />
            </Menu.Item>
          </Menu.Menu>
          {navigations}
        </Menu>
      </div>
    );
  }
}

Navbar.propTypes = {
  logoutTeam: PropTypes.func.isRequired
};

const mapStateToProps = state => ({
  auth: state.auth
});

export default connect(
  mapStateToProps,
  { logoutTeam }
)(Navbar);
