import axios from "axios";
import { GET_ERRORS, SET_GAME_SETTINGS } from "./types";

export const getGameSettings = () => dispatch => {
  axios
    .get("/api/gamesettings")
    .then(res => {
      dispatch({
        type: SET_GAME_SETTINGS,
        payload: res.data
      });
    })
    .catch(err => {
      dispatch({
        type: GET_ERRORS,
        payload: err.response.data
      });
    });
};
