import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { Provider } from "react-redux";
import setAuthToken from "./utils/setAuthToken";
import jwt_decode from "jwt-decode";
import { logoutTeam, setCurrentTeam } from "./actions/authAction";

import "./App.css";
import store from "./store";

import Navbar from "./components/Navbar/Navbar";
import Landing from "./components/Landing/Landing";
import Register from "./components/Register/Register";
import Login from "./components/Login/Login";
import Challenges from "./components/Challenges/Challenges";
import PrivateRoute from "./components/common/PrivateRoute";
import Leaderboard from "./components/Leaderboard/Leaderboard";
import Dashboard from "./components/Dashboard/Dashboard";
import Notifications from "./components/Notifications/Notifications";

if (localStorage.jwt) {
  // Set auth header
  setAuthToken(localStorage.jwt);

  // Decode token
  const teamData = jwt_decode(localStorage.jwt);

  // Set current team in redux store
  store.dispatch(setCurrentTeam(teamData));

  const currentTime = Date.now() / 1000;
  if (teamData.exp < currentTime) {
    // Logut Team
    store.dispatch(logoutTeam());

    // Delete token
    window.location.href = "/login";
  }
}

function App() {
  return (
    <Provider store={store}>
      <Router>
        <div className="App">
          <Navbar />
          <Route exact path="/" component={Landing} />
          <Route exact path="/login" component={Login} />
          <Route exact path="/register" component={Register} />
          <Switch>
            <PrivateRoute exact path="/challenges" component={Challenges} />
          </Switch>
          <Switch>
            <PrivateRoute exact path="/leaderboard" component={Leaderboard} />
          </Switch>
          <Switch>
            <PrivateRoute exact path="/dashboard" component={Dashboard} />
          </Switch>
          <Switch>
            <PrivateRoute
              exact
              path="/notifications"
              component={Notifications}
            />
          </Switch>
        </div>
      </Router>
    </Provider>
  );
}

export default App;
