import React, { Component } from "react";
import { Button, Form, Segment } from "semantic-ui-react";
import { registerTeam } from "../../actions/authAction";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import isEmpty from "../../validation/is-empty";

import "./Register.css";

class Register extends Component {
  state = {
    name: "",
    handle: "",
    email: "",
    password: "",
    confirmPassword: "",
    errors: {}
  };

  componentWillMount() {
    if (this.props.auth.isAuthenticated) {
      this.props.history.push("/dashboard");
    }
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({
        errors: nextProps.errors
      });
    }
  }

  onChangeHandler = event => {
    this.setState({
      [event.target.name]: event.target.value
    });
  };

  onSubmitHandler = event => {
    const newTeam = {
      name: this.state.name,
      handle: this.state.handle,
      email: this.state.email,
      password: this.state.password,
      confirmPassword: this.state.confirmPassword
    };

    this.props.registerTeam(newTeam, this.props.history);
  };

  render() {
    const errors = this.state.errors;
    return (
      <Segment
        raised
        style={{
          fontFamily: "Iceland",
          margin: "100px auto 0 auto",
          width: "500px",
          borderRadius: "10px",
          border: "green"
        }}
      >
        <Form>
          <h1>Register</h1>
          <hr />
          <label>Team Name</label>
          <br />
          {isEmpty(errors.name) ? null : <span>{errors.name}</span>}
          <Form.Input
            fluid
            style={{
              fontFamily: "Iceland",
              fontSize: "15px"
            }}
            placeholder="Team name"
            name="name"
            value={this.state.name}
            onChange={this.onChangeHandler}
            error={!isEmpty(errors.name)}
          />
          <label>Team Handle</label>
          <br />
          {isEmpty(errors.handle) ? null : <span>{errors.handle}</span>}
          <Form.Input
            fluid
            style={{
              fontFamily: "Iceland",
              fontSize: "15px"
            }}
            placeholder="Team handle"
            name="handle"
            value={this.state.handle}
            onChange={this.onChangeHandler}
            error={!isEmpty(errors.handle)}
          />
          <label>E-mail</label>
          <br />
          {isEmpty(errors.email) ? null : <span>{errors.email}</span>}
          <Form.Input
            fluid
            style={{
              fontFamily: "Iceland",
              fontSize: "15px"
            }}
            placeholder="E-mail"
            name="email"
            value={this.state.email}
            onChange={this.onChangeHandler}
            error={!isEmpty(errors.email)}
          />
          <label>Password</label>
          <br />
          {isEmpty(errors.password) ? null : <span>{errors.password}</span>}
          <Form.Input
            fluid
            style={{
              fontFamily: "Iceland",
              fontSize: "15px"
            }}
            placeholder="Password"
            name="password"
            type="password"
            value={this.state.password}
            onChange={this.onChangeHandler}
            error={!isEmpty(errors.password)}
          />
          <label>Confirm Password</label>
          <br />
          {isEmpty(errors.confirmPassword) ? null : (
            <span>{errors.confirmPassword}</span>
          )}
          <Form.Input
            fluid
            style={{
              fontFamily: "Iceland",
              fontSize: "15px"
            }}
            placeholder="Confirm Password"
            name="confirmPassword"
            type="password"
            value={this.state.confirmPassword}
            onChange={this.onChangeHandler}
            error={!isEmpty(errors.confirmPassword)}
          />
          <Button
            inverted
            color="green"
            type="submit"
            style={{
              fontFamily: "Iceland",
              fontSize: "25px",
              padding: "10px",
              width: "100%",
              margin: "10px 0px 0px 0px"
            }}
            onClick={this.onSubmitHandler}
          >
            Submit
          </Button>
        </Form>
      </Segment>
    );
  }
}

Register.propTypes = {
  registerTeam: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  errors: state.errors,
  auth: state.auth
});

export default connect(
  mapStateToProps,
  { registerTeam }
)(Register);
