import React, { Component } from "react";
import "./Landing.css";
import { Container, Header, Icon, Segment } from "semantic-ui-react";
import { connect } from "react-redux";
import { getGameSettings } from "../../actions/gameSettingAction";
import Clock from "../Clock/Clock";

class Landing extends Component {
  componentDidMount() {
    this.props.getGameSettings();
  }
  render() {
    const { settings } = this.props.gameSetting;

    return (
      <Container fluid>
        <Segment inverted>
          <Header
            as="h1"
            style={{
              fontFamily: "Iceland",
              fontSize: "60px",
              margin: "40px 0px 0px 0px",
              color: "white"
            }}
          >
            <Icon name="flag outline" color="green" />
            {settings.event}
          </Header>
          <Header
            as="h1"
            style={{
              fontFamily: "Iceland",
              fontSize: "40px",
              margin: "0px",
              color: "white"
            }}
          >
            {settings.organisation}
          </Header>
        </Segment>
        <Clock start={settings.startTime} end={settings.endTime} />
      </Container>
    );
  }
}

const mapStateToProps = state => ({
  gameSetting: state.gameSetting
});

export default connect(
  mapStateToProps,
  { getGameSettings }
)(Landing);
