import React, { Component } from "react";
import { connect } from "react-redux";
import { getChallenges } from "../../actions/challengeAction";
import "./Dashboard.css";
import {
  Segment,
  Header,
  Container,
  Item,
  Label,
  Table
} from "semantic-ui-react";
import axios from "axios";

class Dashboard extends Component {
  state = {
    teams: [],
    submissions: []
  };
  componentDidMount() {
    this.props.getChallenges();
    axios
      .get("/api/teams/leaderboard")
      .then(res => {
        this.setState({
          teams: res.data
        });
      })
      .catch(err => console.log(err));
    axios
      .get("/api/submissions/")
      .then(res => {
        this.setState({
          submissions: res.data
        });
      })
      .catch(err => console.log(err));
  }
  render() {
    const { team } = this.props.auth;
    const { challenges } = this.props.challenge;
    const { teams } = this.state;
    const teamIndex = teams.findIndex(t => t._id === team.id);
    let score = 0,
      rank = 0;
    if (teamIndex >= 0) {
      score = teams[teamIndex].score;
      rank = teamIndex + 1;
    }
    const colorOptions = [
      "red",
      "orange",
      "olive",
      "green",
      "teal",
      "blue",
      "violet",
      "purple",
      "pink",
      "brown"
    ];
    const categories = [
      ...new Set(challenges.map(challenge => challenge.category))
    ];
    const solvedChallenges = categories.map((category, index) => (
      <Item key={index}>
        <Item.Content>
          <Item.Header
            style={{
              color: colorOptions[index % colorOptions.length],
              fontSize: "25px"
            }}
          >
            {category.toUpperCase()}
          </Item.Header>
          <Item.Description>
            {challenges
              .filter(challenge => challenge.category === category)
              .filter(
                challenge =>
                  challenge.solvedBy.filter(t => t._id === team.id).length > 0
              )
              .map((challenge, ind) => (
                <Label
                  key={ind}
                  color="grey"
                  size="large"
                  style={{ margin: "5px" }}
                >
                  {challenge.name}
                </Label>
              ))}
          </Item.Description>
        </Item.Content>
      </Item>
    ));

    const submissionsContent = this.state.submissions.map(
      (submission, index) => (
        <Table.Row positive={submission.verdict === "Correct"}>
          <Table.Cell>{index + 1}</Table.Cell>
          <Table.Cell>{submission.challenge.name}</Table.Cell>
          <Table.Cell>{submission.verdict}</Table.Cell>
          <Table.Cell>{submission.flag}</Table.Cell>
          {/* <Table.Cell>{submission.date}</Table.Cell> */}
        </Table.Row>
      )
    );

    return (
      <div className="dashboard">
        <Segment
          style={{ margin: "0px", fontFamily: "Iceland" }}
          textAlign="center"
          inverted
          padded
        >
          <Header
            as="h1"
            style={{
              fontSize: "50px",
              fontFamily: "Iceland",
              padding: "0px",
              margin: "10px"
            }}
          >
            {team.name}
          </Header>
          <Header
            as="h3"
            style={{
              fontSize: "25px",
              fontFamily: "Iceland",
              padding: "0px",
              margin: "10px"
            }}
          >
            @{team.handle}
          </Header>
          <Header
            as="h2"
            style={{
              fontSize: "40px",
              fontFamily: "Iceland",
              padding: "0px",
              margin: "10px"
            }}
          >
            Score: {score}
          </Header>
          <Header
            as="h2"
            style={{
              fontSize: "40px",
              fontFamily: "Iceland",
              padding: "0px",
              margin: "10px"
            }}
          >
            Rank: {rank}
          </Header>
        </Segment>
        <Container>
          <Segment raised padded style={{ margin: "10px auto" }}>
            <Header as="h1" centered>
              Solved
            </Header>
            <hr />
            <Item.Group>{solvedChallenges}</Item.Group>
          </Segment>
        </Container>
        <Container>
          <Segment raised padded style={{ margin: "10px auto" }}>
            <Header as="h1" centered>
              Submissions
            </Header>
            <hr />

            <Table color="teal" padded celled>
              <Table.Header>
                <Table.Row>
                  <Table.HeaderCell width={2}>S. No.</Table.HeaderCell>
                  <Table.HeaderCell>Challenge</Table.HeaderCell>
                  <Table.HeaderCell>Verdict</Table.HeaderCell>
                  <Table.HeaderCell>Flag</Table.HeaderCell>
                  {/* <Table.HeaderCell>Timestamp</Table.HeaderCell> */}
                </Table.Row>
              </Table.Header>
              <Table.Body>{submissionsContent}</Table.Body>
            </Table>
          </Segment>
        </Container>
      </div>
    );
  }
}
const mapStateToProps = state => ({
  auth: state.auth,
  challenge: state.challenge
});
export default connect(
  mapStateToProps,
  { getChallenges }
)(Dashboard);
