import React, { Component } from "react";
import { Container, Header, Card, Label } from "semantic-ui-react";
import { connect } from "react-redux";
import { getChallenges } from "../../../actions/challengeAction";

import "./Category.css";
import Challenge from "./Challenge/Challenge";
class Category extends Component {
  state = {
    show: false
  };
  onClickShow = () => {
    this.setState({
      show: !this.state.show
    });
  };
  render() {
    const challenges = this.props.challenges.map((challenge, index) => (
      <Challenge
        color={this.props.color}
        key={index}
        challenge={challenge}
        getChallenges={this.props.getChallenges}
      />
    ));

    let solved = this.props.challenges.filter(
      challenge =>
        challenge.solvedBy.filter(
          team => team._id.toString() === this.props.auth.team.id.toString()
        ).length > 0
    ).length;
    const total = this.props.challenges.length;

    return (
      <div className="category">
        <Container>
          <Header
            onClick={this.onClickShow}
            as="h2"
            color={this.props.color}
            style={{
              fontFamily: "Iceland",
              fontSize: "3em"
            }}
          >
            {this.props.category.toUpperCase()}
            <Label
              pointing="left"
              // circular
              style={{ fontSize: "20px", padding: "8px", margin: "5px 20px" }}
              basic
              color={solved === total ? "green" : "red"}
            >{`Solved: ${solved}/${total}`}</Label>
          </Header>
          {this.state.show ? <Card.Group>{challenges}</Card.Group> : null}
        </Container>
      </div>
    );
  }
}
const mapStateToProps = state => ({
  auth: state.auth
});
export default connect(
  mapStateToProps,
  { getChallenges }
)(Category);
