import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { getChallenges } from "../../actions/challengeAction";
import "./Challenges.css";
import Category from "./Category/Category";
import { Container, Segment, Header } from "semantic-ui-react";
import { getGameSettings } from "../../actions/gameSettingAction";
class Challenges extends Component {
  componentDidMount() {
    this.props.getGameSettings();
    this.props.getChallenges();
  }
  render() {
    let contestRunning = false;

    if (this.props.gameSetting.settings.startTime) {
      const start = new Date(this.props.gameSetting.settings.startTime);
      const end = new Date(this.props.gameSetting.settings.endTime);
      const current = new Date(Date.now());
      contestRunning = current >= start && current <= end;
    }

    if (!contestRunning) {
      return (
        <Container fluid>
          <Segment inverted>
            <Header as="h1"> Contest is over </Header>
          </Segment>
        </Container>
      );
    }
    const { challenge } = this.props;
    const { challenges } = challenge;

    const categories = [
      ...new Set(challenges.map(challenge => challenge.category))
    ];
    const colorOptions = [
      "red",
      "orange",
      "olive",
      "green",
      "teal",
      "blue",
      "violet",
      "purple",
      "pink",
      "brown"
    ];
    const allCategories = categories.map((category, index) => {
      return (
        <Segment
          key={index}
          raised
          style={{
            borderRadius: "25px"
          }}
        >
          <Category
            color={colorOptions[index % colorOptions.length]}
            category={category}
            challenges={challenges.filter(
              challenge => challenge.category === category
            )}
          />
        </Segment>
      );
    });

    return (
      <div className="challenges" style={{ margin: "20px 0px 0px 0px" }}>
        <Container>{allCategories}</Container>
      </div>
    );
  }
}

Challenges.propTypes = {
  getChallenges: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
  challenge: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  auth: state.auth,
  challenge: state.challenge,
  gameSetting: state.gameSetting
});

export default connect(
  mapStateToProps,
  { getChallenges, getGameSettings }
)(Challenges);
