import axios from 'axios';
import setAuthToken from '../utils/setAuthToken';
import jwt_decode from 'jwt-decode';

import { GET_ERRORS, SET_CURRENT_TEAM } from './types';

export const registerTeam = (team , history) => dispatch => {
    axios.post('/api/teams/register' , team)
        .then(team => {
            history.push('/login');
        })
        .catch(err => {
            dispatch({
                type: GET_ERRORS,
                payload: err.response.data
            })
        })
}

export const loginTeam = team => dispatch => {
    axios.post('/api/teams/login' , team)
        .then(res => {
            // Get the token
            const { token } = res.data;

            // Store token in local storage
            localStorage.setItem('jwt' , token);

            // set auth token to headers
            setAuthToken(token);

            // decode token to get user data.
            const teamData = jwt_decode(token);

            // set current team
            dispatch(setCurrentTeam(teamData));
        })
        .catch(err => {
            dispatch({
                type: GET_ERRORS,
                payload: err.response.data
            })
        })
}

export const setCurrentTeam = teamData => {
    return {
        type: SET_CURRENT_TEAM,
        payload: teamData
    }
}

export const logoutTeam = () => dispatch => {
    // Remove token from local storage.
    localStorage.removeItem('jwt');

    // remove auth header
    setAuthToken(false);

    // remove current team from redux state
    dispatch(setCurrentTeam({}));
}
