import React, { Component } from "react";
import { Segment, Header, Message, Container } from "semantic-ui-react";
import { connect } from "react-redux";
import { getNotifications } from "../../actions/notificationsAction";
import Moment from "react-moment";

class Notifications extends Component {
  componentDidMount() {
    this.props.getNotifications();
  }

  render() {
    const { notifications } = this.props.notification;
    const notificationsContent = notifications.map((notification, index) => (
      <Message
        key={index}
        floating
        style={{ margin: "10px 0px", fontFamily: "Iceland", fontSize: "20px" }}
      >
        <Message.Header>
          <Moment format="h:mm a, MMMM DD">{notification.date}</Moment>
        </Message.Header>
        <p style={{ fontSize: "30px" }}>{notification.text}</p>
      </Message>
    ));
    return (
      <div>
        <Segment
          style={{ margin: "0px", fontFamily: "Iceland" }}
          textAlign="center"
          inverted
          padded
        >
          <Header
            as="h1"
            style={{
              fontSize: "50px",
              fontFamily: "Iceland",
              padding: "0px",
              margin: "10px"
            }}
          >
            Notifications
          </Header>
        </Segment>
        <Container>{notificationsContent}</Container>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  notification: state.notification
});

export default connect(
  mapStateToProps,
  { getNotifications }
)(Notifications);
