import axios from "axios";
import { GET_NOTIFICATIONS, GET_ERRORS } from "./types";

export const getNotifications = () => dispatch => {
  axios
    .get("/api/notifications")
    .then(res => {
      dispatch({
        type: GET_NOTIFICATIONS,
        payload: res.data
      });
    })
    .catch(err => {
      dispatch({
        type: GET_ERRORS,
        payload: err.response.data
      });
    });
};
